.home-container {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 10rem;
    color: #5a5959;
}

.home-icons-part {
    font-size: 20px;
}

.home-icons-part ul {
    display: flex;
    flex-direction: column;
}

.home-icons-part ul li {
    margin-top: 1rem;
}


.home-text-part {
    position: relative;
    margin-top: 1rem;
}

.home-text-part h2 {
    color: #28231d;
    font-size: 40px;
    margin-top: 1rem;
}

.home-text-part h3 {
    color: #28231d;
    font-size: 25px;

}

.home-text-part button {
    margin-top: 3rem;
    padding: 1.2rem 3rem;
    background-color: #28231d;
    color: white;
    border-radius: 1rem;
    font-size: 20px;
    cursor: pointer;
    text-align: center;
}

.home-text-part button:hover {
    background-color: black;
}


