@keyframes profile__animate {
    0% {
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }

    50% {
        border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
    }

    100% {
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
}

.scroll-down {
    margin-top: 4rem;
    font-size: 15px;

}

.scroll-down a {
    text-decoration: none;

}

.scroll-down span {
    font-size: 15px;
    margin-left: 0.5rem;

}

.wheel {
    animation: scroll 2s ease infinite;
}

@keyframes scroll {
    0% {
        transform: translateY(0);
    }

    30% {
        transform: translateY(3.72rem);
    }
}


/* Media Queries =========> */

@media (max-width: 768px) {
    .home-container {
        flex-direction: column;
        margin-top: 5rem;
    }

    .home-icons-part {
        font-size: 16px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 1rem;
    }

    .home-icons-part ul {
        display: flex;
        flex-direction: row;
        margin: 0;
        padding: 0;
        list-style: none;
    }

    .home-icons-part ul li {
        margin: 0 1rem;
    }

    /* .home-image-part {
        width: 80%;
        max-width: 300px;
        height: 200px;
        order: -1;
    }

    .home-image-part img {
        width: 100%;
        height: auto;
        box-shadow: inset 0 0 0 9px rgba(255, 255, 255, 0.3);
        animation: profile__animate 8s ease-in-out infinite 1s;
    } */

    .home-text-part {
        text-align: center;
        margin-top: 2rem;
    }

    .home-text-part h2 {
        font-size: 28px;
        margin-top: 1rem;
    }

    .home-text-part span {
        font-size: 18px;
    }

    .home-text-part button {
        padding: 1rem 2rem;
        font-size: 14px;
        margin-top: 2rem;
    }

    .scroll-down {
        margin-top: 2rem;
    }
}

@media (max-width: 480px) {
    .home-text-part h2 {
        font-size: 24px;
    }

    .home-text-part h3 {
        font-size: 20px;
    }

    .home-text-part span {
        font-size: 16px;
    }


    .home-text-part button {
        padding: 0.7rem 1rem;
        font-size: 15px;
        margin-top: 1.5rem;
    }

